"use scrict";

export default {
	name: "MyFormItem",
	props: {
		label: {
			type: String,
			default: ""
		},
		direction: {
			type: String,
			default: "row"
		},
		hasClear: {
			type: Boolean,
			default: true
		}
	},
	computed: {
		labelWidth() {




			return this.$parent.labelWidth;

		}
	},
	methods: {
		clear: function () {
			this.$emit("clear");
		},
		click: function () {
			this.$emit("click");
		}
	}
}
